.container {
    width: 480px;
    max-width: 480px;
    height: 260px;
    overflow: hidden;
    margin: 0px 7px 14px 7px;
    cursor: pointer;
    flex-direction: row;
    flex: 200px auto;

    .image {
        max-width: 200px;
        // max-height: 200px;
        img {
            width: 100%;
            object-fit: cover;
        }
    }

    &:hover {
        transform: scale(1.05);
        transition: all 0.3s;
    }
}
